import React, { useEffect, useState } from "react";
import axios from "axios";
import { useNavigate, useLocation, useParams } from "react-router-dom";
import {
  Button,
  Form,
  Grid,
  Header,
  Message,
  Segment,
} from "semantic-ui-react";

const AddEdit = () => {
  var token = "";
  var enrollmentId = "";
  var planId = "";
  const navigate = useNavigate();

  const [details, setState] = useState({
    email: "",
    fName: "",
    lName: "",
    mName: "",
    dob: "",
    ssn: "",
    zipCode: "",
    areaState: "",
    city: "",
    address: "",
    phoneNumber: "",
  });

  const handleSubmit = async (e) => {
    e.preventDefault();

    const response = await axios.post("/authenticate", {
      vendor_id: "MaxSip",
      username: "MaxSipUser",
      password: "MaxSipbtK123hyutrw22",
      pin: "987659876522",
      agent_id: "ewebsiteapi",
      source: "WEBSITE",
    });

    if (response.status === 200) {
      console.log("Response 1 from Api:> ", response.data);

      await checkServices(response.data);
    }
  };

  async function checkServices(token) {
    const response = await axios.post("/enrollment", {
      token: token,
      zipCode: details.zipCode,
    });
    if (response.status === 200) {
      console.log("Response 2 from Api:> ", response.data);
      console.log(
        "Enrollment Id  from Api:> ",
        response.data.data.enrollment_id
      );

      enrollmentId = response.data.data.enrollment_id;
      await planList(response.data.token);
    }
  }

  const planList = async (token) => {
    const response = await axios.post(
      "/plan",

      {
        token: token,
        zipCode: details.zipCode,
      }
    );
    if (response.status === 200) {
      console.log("Response 3 from Api:> ", response.data.data[0].plan_id);
      planId = response.data.data[0].plan_id;

      verifyCustomerDetails(response.data.token);
    }
  };

  const verifyCustomerDetails = async (token) => {
    const response = await axios.post(
      "/verifyCustomerDetails",

      {
        token: token,
        email: details.email,
        pNumber: details.phoneNumber,
        eId: enrollmentId,
      }
    );
    if (response.status === 200) {
      console.log("Response 4 from Api:> ", response.data.token);
      getEnrollmentDetails(response.data.token);
    }
  };

  const getEnrollmentDetails = async (token) => {
    const response = await axios.post(
      "/getEnrollmentDetails",

      {
        token: token,
        zipCode: details.zipCode,
        email: details.email,
      }
    );
    if (response.status === 200) {
      console.log("Response 5 from Api:> ", response.data);
      console.log("Response 5 from Api:> ", response.data.msg);
      console.log("Response 5 from Api:> ", response.data.errors[0]);

      if (response.data.msg == "Error occurred") {
        if (response.data.errors[0] == "No record found!") {
          updateEnrollmentDetails(response.data.token);
        }
      } else {
        console.log("Already Signed..!!!");
      }
    }
  };

  const updateEnrollmentDetails = async (token) => {
    const response = await axios.post(
      "/updateEnrollmentDetails",

      {
        token: token,
        email: details.email,
        firstName: details.firstName,
        lastName: details.lastName,
        min: details.mName,
        dob: details.dob,
        ssn: details.ssn,
        pPhone: details.phoneNumber,
        address: details.address,
        city: details.city,
        sState: details.areaState,
        zipCode: details.zipCode,
        eId: enrollmentId,
        pId: planId,
      }
    );
    if (response.status === 200) {
      console.log("Response 6 from Api:> ", response.data);

      validateAddress(response.data.token);
    }
  };

  const validateAddress = async (token) => {
    const response = await axios.post(
      "/validateAddress",

      {
        token: token,
        email: details.email,
        firstName: details.fName,
        lastName: details.lName,
        min: details.mName,
        dob: details.dob,
        ssn: details.ssn,
        pPhone: details.phoneNumber,
        address: details.address,
        city: details.city,
        sState: details.areaState,
        zipCode: details.zipCode,
        eId: enrollmentId,
        pId: planId,
      }
    );
    if (response.status === 200) {
      console.log("Response 7 from Api:> ", response.data);
      programList(response.data.token);
    }
  };

  const programList = async (token) => {
    const response = await axios.post(
      "/programList",

      {
        token: token,
        zipCode: details.zipCode,
      }
    );
    if (response.status === 200) {
      console.log("Response 8 from Api:> ", response.data);
      eligibiltyCheck(response.data.token);
    }
  };

  const eligibiltyCheck = async (token) => {
    const response = await axios.post(
      "/eligibiltyCheck",

      {
        token: token,
        email: details.email,
        firstName: details.fName,
        lastName: details.lName,
        min: details.mName,
        dob: details.dob,
        ssn: details.ssn,
        pPhone: details.phoneNumber,
        address: details.address,
        city: details.city,
        sState: details.areaState,
        zipCode: details.zipCode,
        eId: enrollmentId,
        pId: planId,
      }
    );
    if (response.status === 200) {
      console.log("Response 9 from Api:> ", response.data);
      eligibiltyStatusCheck(response.data.token);
    }
  };

  const eligibiltyStatusCheck = async (token) => {
    const response = await axios.post("/eligibiltyStatusCheck", {
      token: token,
      email: details.email,
      firstName: details.fName,
      lastName: details.lName,
      min: details.mName,
      dob: details.dob,
      ssn: details.ssn,
      pPhone: details.phoneNumber,
      address: details.address,
      city: details.city,
      sState: details.areaState,
      zipCode: details.zipCode,
      eId: enrollmentId,
      pId: planId,
    });
    if (response.status === 200) {
      console.log("Response 10 from Api:> ", response.data);
      verifyACP(response.data.token);
    }
  };
  const verifyACP = async (token) => {
    const response = await axios.post("/verifyACP", {
      token: token,
      email: details.email,
      firstName: details.fName,
      lastName: details.lName,
      min: details.mName,
      dob: details.dob,
      ssn: details.ssn,
      pPhone: details.phoneNumber,
      address: details.address,
      city: details.city,
      sState: details.areaState,
      zipCode: details.zipCode,
      eId: enrollmentId,
      pId: planId,
    });
    if (response.status === 200) {
      console.log("Response 11 from Api:> ", response.data);
      getPlanList(response.data.token);
    }
  };

  const getPlanList = async (token) => {
    const response = await axios.post(
      "/getPlanList",

      {
        token: token,
        zipCode: details.zipCode,
      }
    );
    if (response.status === 200) {
      console.log("Response 12 from Api:> ", response.data);

      createCustomer(response.data.token, response.data.data[0].plan_id);
    }
  };
  const createCustomer = async (token, pi) => {
    const response = await axios.post(
      "/createCustomer",

      {
        token: token,
        email: details.email,
        firstName: details.fName,
        lastName: details.lName,
        min: details.mName,
        dob: details.dob,
        ssn: details.ssn,
        pPhone: details.phoneNumber,
        address: details.address,
        city: details.city,
        sState: details.areaState,
        zipCode: details.zipCode,
        eId: enrollmentId,
        pId: pi,
      }
    );
    if (response.status === 200) {
      console.log("Response 13 from Api:> ", response.data);
    }
  };

  const handleChange = (e) => {
    const name = e.target.name;
    const value = e.target.value;
    setState((prev) => {
      return { ...prev, [name]: value };
    });
  };
  return (
    <div className="container">
      <form id="contact" onSubmit={handleSubmit}>
        <h3>Maxsip Enrollment Form</h3>
        <fieldset>
          <input
            placeholder="Your Email"
            type="email"
            name="email"
            onChange={handleChange}
          />
        </fieldset>
        <fieldset>
          <input
            placeholder="First Name"
            type="text"
            name="fName"
            onChange={handleChange}
          />
        </fieldset>
        <fieldset>
          <input
            placeholder="Last Name"
            type="text"
            name="lName"
            onChange={handleChange}
          />
        </fieldset>
        <fieldset>
          <input
            placeholder="Middle Initial Name"
            type="text"
            name="mName"
            onChange={handleChange}
          />
        </fieldset>
        <fieldset>
          <input
            placeholder="DOB"
            type="text"
            name="dob"
            onChange={handleChange}
          />
        </fieldset>
        <fieldset>
          <input
            placeholder="SSN (last 4 digit)"
            type="text"
            name="ssn"
            onChange={handleChange}
          />
        </fieldset>
        <fieldset>
          <input
            placeholder="Address"
            type="text"
            name="address"
            onChange={handleChange}
          />
        </fieldset>
        <fieldset>
          <input
            placeholder="City"
            type="text"
            name="city"
            onChange={handleChange}
          />
        </fieldset>
        <fieldset>
          <input
            placeholder="State"
            type="text"
            name="areaState"
            onChange={handleChange}
          />
        </fieldset>
        <fieldset>
          <input
            placeholder="Zip Code"
            type="text"
            name="zipCode"
            onChange={handleChange}
          />
        </fieldset>
        <fieldset>
          <input
            placeholder="Your Phone No"
            type="tel"
            name="phoneNumber"
            onChange={handleChange}
          />
        </fieldset>
        <Button color="black" fluid size="large" type="submit" className="btn">
          {" "}
          Login
        </Button>
        <fieldset></fieldset>
      </form>
    </div>
  );
};

export default AddEdit;
